.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.ant-collapse-borderless {
  padding: 0 0.0625rem !important;
  background-color: #fff !important;
}
.ant-collapse-borderless .ant-collapse-item {
  position: relative;
  margin-bottom: 0.625rem;
  border: 0.0625rem solid #f1ecec;
  border-bottom: 0.0625rem solid #f1ecec;
  border-radius: 0.625rem !important;
}
.ant-collapse-borderless .ant-collapse-item .ant-collapse-header {
  padding-top: 0.625rem !important;
  padding-bottom: 0.625rem !important;
  border-bottom: 0.0625rem solid #f1ecec;
  border-radius: 0.625rem 0.625rem 0 0;
}
.ant-collapse-borderless .ant-collapse-item .ant-collapse-content-box {
  padding: 1rem 0 !important;
}
.ant-collapse-borderless .ant-descriptions {
  width: 90%;
  margin: 0 auto;
  padding: 0;
}
.print {
  display: none;
}
@media print {
  .print {
    display: block !important;
  }
}
