.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-eprescription-wrapper .ant-tabs-bar {
  border-bottom: 0 !important;
  box-shadow: none !important;
}
.umi-eprescription-wrapper .ant-tabs-ink-bar {
  display: none !important;
}
.umi-eprescription-wrapper .ant-checkbox-wrapper {
  margin-bottom: 0.625rem;
}
.umi-eprescription-wrapper .umi-title {
  margin-bottom: 0.625rem;
  font-weight: 600;
}
.umi-eprescription-wrapper .umi-lab-radio {
  margin-bottom: 0.625rem;
}
.umi-eprescription-wrapper .umi-lab-radio .ant-radio-group {
  display: inline-flex;
}
.umi-eprescription-wrapper .umi-lab-radio .umi-radio-label {
  margin-right: 0.9375rem;
}
.umi-eprescription-wrapper .ant-checkbox-wrapper {
  display: block;
}
.umi-eprescription-wrapper .umi-vic-checkbox {
  margin-bottom: 0.625rem;
}
.umi-eprescription-wrapper .umi-vic-checkbox .ant-checkbox-wrapper {
  margin-bottom: 0.3125rem;
}
.umi-eprescription-wrapper .title-add-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
