.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-medication-report {
  margin-bottom: 0.625rem;
  padding: 0 1.25rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 0.625rem;
}
.umi-medication-report .umi-patient-info {
  padding: 0.9375rem 0;
  border-top: 0.125rem solid #551872;
  border-bottom: 0.125rem solid #551872;
  font-weight: 600;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.umi-medication-report .umi-descripttion {
  min-height: 12.5rem;
  padding: 1.875rem 0;
  font-weight: 600;
}
.umi-medication-report .umi-descripttion .umi-description-title {
  margin-bottom: 0.9375rem;
  font-size: 1.125rem;
}
.umi-medication-report .umi-descripttion ul {
  padding-left: 0.625rem;
}
.umi-medication-report .umi-descripttion li {
  margin-bottom: 0.9375rem;
  display: flex;
  justify-content: space-between;
}
.umi-medication-report .umi-descripttion li:last-child {
  margin-bottom: 0;
}
.umi-medication-report .ant-descriptions {
  padding: 1rem 0 0 0;
  border-top: 0.125rem solid #551872;
  border-bottom: 0.125rem solid #551872;
}
.umi-medication-report .prescription-list {
  width: 92%;
}
.umi-medication-report .umi-remove-medicine,
.umi-medication-report .umi-edit-medicine {
  cursor: pointer;
}
.umi-medication-report .umi-remove-medicine {
  color: #c90000;
}
.umi-medication-report .umi-edit-medicine {
  margin-right: 0.625rem;
}
