.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.ant-card.ant-card-bordered {
  border-radius: 0.875rem;
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.ant-card.ant-card-bordered ul {
  margin-bottom: 1.25rem;
}
.ant-card.ant-card-bordered li {
  margin-bottom: 0.625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ant-card.ant-card-bordered li .umi-title-date {
  width: 65%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ant-card.ant-card-bordered li .umi-date,
.ant-card.ant-card-bordered li .umi-months {
  font-size: 0.75rem;
}
.ant-card.ant-card-bordered li .umi-date {
  color: #a1a0a0;
}
.ant-card.ant-card-bordered li .umi-months {
  color: #551872;
}
.ant-card.ant-card-bordered .umi-btn-wrapper {
  text-align: center;
}
.ant-card.ant-card-bordered .umi-btn-wrapper .umi-btn {
  padding: 0.375rem 1.25rem;
  border: 1px solid #f1ecec;
  background-color: #fff;
  color: #3d3d3d;
}
.ant-card.ant-card-bordered .umi-btn-wrapper .umi-btn:hover,
.ant-card.ant-card-bordered .umi-btn-wrapper .umi-btn:active {
  border-color: #551872;
  color: #fff;
}
.ant-card.ant-card-bordered .umi-btn-wrapper .umi-btn-complete {
  border-color: #551872;
  background-color: transparent;
  color: #551872;
}
.ant-card.ant-card-bordered .umi-btn-wrapper .umi-btn-complete:hover {
  background-color: #551872;
  color: #fff;
}
.ant-card.umi-complain-draft {
  background-color: #f0f0f0;
}
.ant-card.umi-complain-draft .ant-card-head {
  border-bottom: 1px solid #a1a0a0;
}
