.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.panel-input-wrapper {
  width: 90%;
  margin: 0 auto;
}
.panel-input-wrapper .umi-form-title {
  margin-bottom: 0.625rem;
  font-weight: 600;
}
.panel-input-wrapper .ant-input {
  margin-bottom: 0.625rem;
}
.panel-input-wrapper textarea {
  padding: 0.625rem 3.125rem 0.625rem 0.625rem;
}
.panel-input-wrapper .umi-status-title {
  margin-top: 0.625rem;
  margin-bottom: 0.3125rem;
}
.panel-input-wrapper .umi-examination-checkbox {
  margin-bottom: 0.625rem;
}
.panel-input-wrapper .other-textarea {
  margin-top: 0.625rem;
}
.panel-input-wrapper .umi-status-data {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.panel-input-wrapper .umi-status-data .ant-checkbox-checked .ant-checkbox-inner {
  background-color: #07cd15;
  border-color: #07cd15;
}
.panel-input-wrapper .umi-status-data .ant-checkbox-inner::after {
  border: 0;
}
.panel-input-wrapper .umi-btn.small {
  width: 100%;
  margin-top: 0.625rem;
  padding: 0.75rem 0;
}
.panel-input-wrapper .umi-system-review .umi-review-tilte-wrapper {
  padding: 0.625rem 0;
  display: flex;
}
.panel-input-wrapper .umi-system-review .umi-icon-wrapper {
  margin-right: 1.25rem;
}
.panel-input-wrapper .umi-system-review .ant-checkbox-wrapper {
  margin-left: 0;
  margin-bottom: 0.625rem;
  display: block;
}
.panel-input-wrapper .umi-system-review .umi-change-icon .anticon-flag {
  color: #f00;
}
.panel-input-wrapper .umi-textarea-icons .handwriting {
  width: 0.9375rem;
}
.panel-input-wrapper .speech-recognation {
  margin-left: 0.625rem;
  display: inline-block;
}
.umi-external-referral {
  display: grid;
  grid-template-columns: 180px auto;
  align-items: center;
}
.umi-external-referral .ant-input {
  margin-bottom: 0;
}
