.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.title {
  margin-bottom: 0.9375rem;
  padding: 0.75rem 1.875rem;
  background-color: #f0f0f0;
  font-size: 1rem;
  font-weight: 600;
}
.presenting-data .ant-descriptions-row {
  display: flex;
  flex-direction: column;
}
.umi-patient-examination-info {
  padding: 0 1.875rem;
}
.umi-patient-examination-info .umi-examination-status {
  margin-bottom: 0.625rem;
}
.umi-patient-examination-info .umi-examination-par {
  margin-bottom: 1.25rem;
}
.ant-form label {
  width: 50%;
}
label.ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin-bottom: 0.3125rem;
  margin-left: 0;
}
@media print {
  @page {
    size: auto;
    margin: 5%;
  }
}
