.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-report-wrapper {
  width: 90%;
  margin: 0 auto;
}
.umi-report-wrapper .umi-title {
  padding: 0.625rem 0 1.25rem;
}
.umi-report-wrapper .umi-patient-details-wrapper {
  padding: 0 1.25rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 0.625rem;
}
.umi-report-wrapper .umi-patient-title {
  padding: 1rem 1.875rem;
  font-size: 1rem;
  font-weight: 600;
  background-color: #f0f0f0;
}
.umi-report-wrapper .ant-descriptions {
  width: 100%;
  padding: 1.25rem 1.875rem 0.25rem;
}
.umi-report-wrapper .umi-patient-examination-info,
.umi-report-wrapper .umi-patient-content {
  padding: 1.25rem 1.875rem;
}
.umi-report-wrapper .umi-patient-content ul {
  margin-left: 1.875rem;
  list-style-type: disc;
}
.umi-report-wrapper .umi-patient-content li {
  margin: 0.625rem;
}
.umi-report-wrapper .umi-doctor-signture {
  font-size: 1rem;
  font-weight: 600;
  display: grid;
  justify-items: end;
}
.umi-report-wrapper .umi-signture {
  margin-bottom: 0.625rem;
}
