.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-doctor-info {
  padding: 1.5625rem 0;
}
.umi-doctor-info .umi-doctor-name {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.umi-doctor-info h2 {
  color: #551872;
}
.umi-doctor-info .umi-logo {
  display: grid;
  justify-items: end;
}
.umi-doctor-info img {
  height: 1.875rem;
}
