.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-clinic-start-content {
  padding: 1.25rem 0;
  background-color: #fff;
}
.umi-clinic-start-content img {
  width: 100%;
  object-fit: cover;
}
@media (max-width: 991px) {
  .umi-clinic-start-content {
    padding-bottom: 0;
  }
}
@media (max-width: 575px) {
  .umi-next-patient,
  .umi-paused-patients {
    margin-bottom: 0.625rem;
  }
  .umi-next-patient,
  .umi-paused-patients,
  .umi-start-patient figure {
    padding: 0.3125rem;
  }
  .umi-countdown-wrapper {
    padding: 0.3125rem 0;
  }
  .umi-resarvation-wrapper .umi-sort-reservation {
    padding: 1.125rem 0.3125rem;
  }
  .umi-next-patient .ant-avatar-image {
    margin-right: 0.625rem;
  }
}
