.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-resarvation-wrapper {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.umi-resarvation-wrapper .umi-patients-type {
  border-bottom: 0.0625rem solid #f1ecec;
  display: flex;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs {
  width: 100%;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-bar {
  margin-bottom: 0 !important;
  box-shadow: none;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-ink-bar {
  display: none !important;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-nav {
  width: 100%;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-nav .ant-tabs-tab {
  width: 50%;
  margin: 0;
  text-align: center;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-nav .ant-tabs-tab:first-child {
  border-right: 0.0625rem solid #f1ecec;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-nav .ant-tabs-tab:after {
  margin-left: 0.5rem;
  padding: 0.1875rem 0.3125rem;
  border-radius: 0.4375rem;
  font-size: 0.8125rem;
  font-weight: 500;
  background-color: #eee7f1;
  color: #551872;
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-nav .ant-tabs-tab:first-child:after {
  content: '10';
}
.umi-resarvation-wrapper .umi-patients-type .ant-tabs-nav .ant-tabs-tab:last-child:after {
  content: '15';
}
.umi-resarvation-wrapper .umi-sort-reservation {
  padding: 1.125rem 1.25rem;
}
.umi-resarvation-wrapper .umi-sort-reservation .ant-select:hover .ant-select-selector,
.umi-resarvation-wrapper .umi-sort-reservation .ant-select:focus .ant-select-selector,
.umi-resarvation-wrapper .umi-sort-reservation .ant-select:active .ant-select-selector {
  border-color: #551872;
  color: #551872;
  outline: 0;
}
.umi-resarvation-wrapper .umi-sort-reservation .ant-select-selector {
  height: 2.375rem !important;
  border-radius: 0.625rem !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-resarvation-wrapper .umi-sort-reservation .ant-dropdown-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #3d3d3d;
}
.umi-resarvation-wrapper .umi-sort-reservation .umi-content-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-resarvation-wrapper .umi-sort-reservation .umi-img-wrapper {
  width: 1.125rem;
  margin-right: 0.3125rem;
}
.umi-resarvation-wrapper .umi-sort-reservation .umi-patient-reservation-wrapper {
  margin-top: 1.25rem;
}
