.container {
  width: 85%;
  margin: auto;
}
.container-fluid {
  width: 95%;
  margin: auto;
}
.btn-block {
  width: 100%;
}
.btn-light {
  color: #41085c;
  background-color: #fff;
}
.btn-outline {
  color: #41085c;
  background-color: #fff;
  border: 1px solid #41085c;
}
.btn-outline:hover {
  background-color: #41085c;
  color: #fff;
  border: 1px solid #41085c;
}
.btn-purble {
  background-color: #41085c;
  color: #fff;
}
.btn-purble:hover {
  color: #41085c;
  border-color: #41085c;
}
.squared-btn {
  text-transform: capitalize;
  color: #fff;
  background-color: #520974;
  border-radius: 0.375rem;
  width: 7.5rem;
  height: 2.5rem;
  border: unset;
}
.squared-btn:hover {
  box-shadow: 0 0.9375rem 1.25rem #ecdaf6b5;
  border: unset;
  color: #fff;
  background-color: #520974;
}
.img-fluid {
  max-width: 100%;
  max-height: 100%;
}
.ant-input {
  height: 1.875rem;
  background-color: #fafafa;
  margin-bottom: unset !important;
}
.ant-input-prefix {
  margin-right: 0.625rem;
}
.ant-input-prefix .anticon {
  font-size: 0.9375rem;
  color: #520974;
}
.ant-input-affix-wrapper {
  display: flex;
  align-items: center;
  border-radius: 0.625rem;
  background-color: #fafafa;
  margin-bottom: unset !important;
  height: 2.5rem;
}
.ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper:hover {
  border-color: #520974;
  box-shadow: unset;
}
.ant-input-affix-wrapper > input.ant-input {
  height: 2.375rem;
}
.ant-input-password-icon {
  font-size: 0.9375rem;
  color: #520974;
}
.forget-password a {
  color: #520974;
  cursor: pointer;
  font-size: 0.8125rem;
}
.ant-radio-group {
  display: flex;
}
.ant-radio-button-wrapper {
  height: 2.5rem;
  background-color: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ant-radio-button-wrapper img {
  max-width: 100%;
  max-height: 100%;
  -webkit-filter: opacity(0.2);
          filter: opacity(0.2);
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  border-color: #e8e6f1;
  background-color: #fafafa;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):first-child {
  border-color: unset;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):hover {
  border-color: #e8e6f1;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):active {
  border-color: unset;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):focus-within {
  box-shadow: unset;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled)::before {
  background-color: #e8e6f1;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) img {
  -webkit-filter: opacity(1);
          filter: opacity(1);
}
.ant-radio-button-wrapper:first-child {
  border-top-left-radius: 0.625rem;
  border-bottom-left-radius: 0.625rem;
}
.ant-radio-button-wrapper:last-child {
  border-top-right-radius: 0.625rem;
  border-bottom-right-radius: 0.625rem;
}
.ant-radio-button-wrapper-checked:not([class*=" ant-radio-button-wrapper-disabled"]).ant-radio-button-wrapper:first-child {
  border-right-color: unset !important;
  border-left: unset;
}
.ant-picker,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  height: 2.5rem;
  border-radius: 0.625rem;
}
.ant-picker:hover,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector:hover {
  border: 0.0625rem solid #520974;
}
.ant-picker .ant-picker-suffix,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector .ant-picker-suffix {
  color: #520974;
}
textarea.ant-input {
  min-height: 4.375rem;
  margin-bottom: 0.9375rem !important;
}
.ant-select-dropdown {
  border-radius: 0.625rem;
}
.ant-select-multiple .ant-select-selector {
  border-radius: 0.625rem;
  border: 0.0625rem solid #e8e6f1;
  background-color: #fafafa;
  height: auto;
  min-height: 2.5rem;
}
.ant-select-multiple .ant-select-selector:hover {
  border: 0.0625rem solid #520974;
  box-shadow: unset;
}
.ant-select-multiple .ant-select-selection-item {
  background: #ece1ed;
}
.ant-select-item {
  border-bottom: 0.0625rem solid #e8e6f1;
}
.ant-select-item:last-child {
  border-bottom: unset;
}
.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  background-color: #ece1ed;
}
.ant-select-item-option-selected:not(.ant-select-item-option-disabled) .ant-select-item-option-state {
  color: #ff598b;
}
.ant-form-item-has-error .ant-form-item-explain,
.ant-form-item-has-error .ant-form-item-split {
  font-size: 0.75rem;
}
.ant-input:-ms-input-placeholder {
  font-size: 0.84375rem;
  font-weight: 500;
  color: #520974;
  font-family: "Qanelas-regular";
  text-transform: capitalize;
}
.ant-input:placeholder-shown {
  font-size: 0.84375rem;
  font-weight: 500;
  color: #520974;
  font-family: "Qanelas-regular";
  text-transform: capitalize;
}
.ant-input-number {
  width: 100%;
  height: 2.5rem;
  border-radius: 0.625rem;
  background-color: #fafafa;
}
.ant-input-number:hover,
.ant-input-number:focus {
  border: 0.0625rem solid #520974;
  box-shadow: unset;
}
.carousel-item {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #e2e2e2;
  border-radius: 10px;
  transition: all 0.5s;
}
.hover-shadow {
  box-shadow: 0px 3px 6px #4f2b795e;
}
.arrows {
  bottom: 100%;
  top: unset;
  background-image: url("/imgs/arrow.png");
  width: 50px;
  height: 50px;
}
.arrows-prev {
  right: 4%;
  bottom: 106.8%;
  left: unset;
  transform: rotate(180deg);
}
.slick-prev:before,
.slick-next:before {
  color: transparent;
}
.react-tel-input {
  width: 100%;
  height: 2.5rem;
  border: 0.0625rem solid #e8e6f1;
  background-color: #fafafa;
  border-radius: 0.625rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.react-tel-input .flag-dropdown {
  border: unset !important;
  border-radius: 0.625rem !important;
  position: unset !important;
  width: 20%;
  height: 2.5rem;
  order: 1;
}
.react-tel-input .form-control {
  padding-left: unset !important;
  border: unset !important;
  background: unset !important;
  order: 2;
  width: 80% !Important;
}
.react-tel-input .flag-dropdown:hover .selected-flag {
  background-color: #fafafa !important;
}
.react-tel-input .flag-dropdown:focus .selected-flag {
  background-color: #fafafa !important;
}
.phone-group {
  background-color: #fafafa;
  border-radius: 0.625rem;
  height: 2.625rem;
  border: 0.0625rem solid #e8e6f1;
}
.phone-group:hover {
  border: 0.0625rem solid #520974;
}
.phone-group-flag .react-tel-input {
  border: unset;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: unset;
  border-right: 0.0625rem solid #e8e6f1;
  border-top-left-radius: 0.625rem;
  border-bottom-left-radius: 0.625rem;
}
.phone-group-flag .react-tel-input .form-control {
  width: unset;
}
.phone-group-flag .react-tel-input .flag-dropdown {
  width: unset;
}
.phone-group-flag .react-tel-input .flag-dropdown:hover .selected-flag,
.phone-group-flag .react-tel-input .flag-dropdown:focus .selected-flag {
  border-radius: 1.25rem;
}
.phone-group-input .ant-input {
  border: unset;
}
.phone-group-input .ant-input:hover,
.phone-group-input .ant-input:focus {
  border-color: unset;
}
.auth-success-subscription {
  margin: 3.125rem 0;
}
.auth-success-subscription .subscription-cards {
  display: flex;
  flex-direction: row;
  background-color: #fafafa;
  padding: 0.625rem;
  border-radius: 1.25rem;
  box-shadow: 0px 20px 15px #5d656a49;
}
.auth-success-subscription .subscription-heads .subscription-card {
  border-radius: 1.25rem;
}
.auth-success-subscription .subscription-heads .subscription-type p {
  box-shadow: unset;
}
.auth-success-subscription .subscription-back {
  margin-top: 1.25rem;
  text-align: left;
}
.auth-success-subscription .subscription-back span,
.auth-success-subscription .subscription-back .and-icon {
  cursor: pointer;
  color: #520974;
}
.auth-success-subscription .ant-tabs-nav {
  width: 100%;
}
.auth-success-subscription .ant-tabs-nav div {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.auth-success-subscription .ant-tabs-nav div div {
  width: 50%;
}
.auth-success-subscription .ant-tabs-nav .ant-tabs-tab-active {
  color: #520974;
}
.auth-success-subscription .ant-tabs-nav .ant-tabs-tab:hover {
  color: #520974;
}
.auth-success-subscription .ant-tabs-ink-bar {
  background-color: #520974;
  width: 50%;
}
.auth-success-subscription .ant-divider-horizontal.ant-divider-with-text-center {
  font-weight: 800;
  padding-bottom: 1.25rem;
}
