.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.search-input-wrapper {
  position: relative;
}
.search-input-wrapper .search-input-list {
  position: absolute;
  overflow-y: auto;
  top: 2.8125rem;
  right: 0;
  left: 0;
  z-index: 2;
  max-height: 16.25rem;
  padding: 0.3125rem 0;
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.search-input-wrapper .search-iput-item {
  padding: 0.3125rem 0.625rem;
  display: grid;
  grid-template-columns: 95% 5%;
}
.search-input-wrapper .search-iput-item:hover {
  background-color: #eee7f1;
}
.search-input-wrapper .favorite-item {
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.search-input-wrapper .favorite-item.active .anticon-heart {
  color: #c90000;
}
.search-input-wrapper .anticon-heart {
  font-size: 1.25rem;
}
.search-input-wrapper .anticon-heart:hover {
  color: #c90000;
}
.search-input-wrapper .ant-spin-dot-item {
  background-color: #551872;
}
.search-input-wrapper .ant-spin {
  color: #551872;
}
.search-input-wrapper .ant-spin-blur {
  opacity: 0;
}
.search-input-wrapper .list-title {
  padding-right: 0.625rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.search-input-wrapper .list-title:hover {
  white-space: normal;
}
.search-input-wrapper .list-background {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 1;
}
.search-input-wrapper .ant-spin-nested-loading {
  position: static;
}
