.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-session-complain .ant-tag {
  position: absolute;
  top: 0.5rem;
  left: 33%;
  padding: 0.125rem 0.75rem;
  border: 0;
  border-radius: 0.875rem;
  font-size: 0.6875rem;
  font-family: umiFontMed;
}
.ant-tag.ant-tag-red {
  width: 3.75rem;
  height: 1.5625rem;
  margin: 0;
  border-radius: 0.6875rem;
  border-color: transparent;
  font-size: 0.6875rem;
  line-height: 1.5625rem;
  text-align: center;
  font-family: umiFontMed;
  font-weight: 600;
  letter-spacing: 0.0375rem;
}
