.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-referral-wrapper .umi-textarea-icons {
  display: none;
}
.umi-referral-section {
  width: 90%;
  margin: 0 auto;
}
.umi-referral-section form {
  width: 100%;
  margin-bottom: 1.25rem;
}
.umi-print-title {
  margin-bottom: 1.25rem;
  font-weight: 600;
}
.umi-print-report {
  padding: 1.25rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 0.625rem;
}
.umi-print-report span {
  margin-right: 20px;
}
.umi-print-report .umi-title-date {
  margin-bottom: 1.25rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
.umi-print-report .umi-title-date img {
  height: 1.875rem;
}
.umi-print-report .umi-date {
  display: flex;
  justify-content: flex-end;
}
.umi-print-report .umi-date-now,
.umi-print-report .umi-patinet-name,
.umi-print-report .umi-patient-number,
.umi-print-report .umi-patient-complain {
  height: 1.375rem;
  padding: 0 1.25rem;
  background-color: #f0f0f0;
}
.umi-print-report .umi-report-par {
  margin-bottom: 1.25rem;
  font-weight: 600;
}
.umi-print-report .umi-patient-data {
  margin-bottom: 1.25rem;
}
.umi-print-report .umi-patient-info {
  margin-bottom: 0.625rem;
  display: grid;
  grid-template-columns: 70% 30%;
  align-items: center;
}
.umi-print-report .umi-patinet-name-wrapper,
.umi-print-report .umi-patient-number-wrapper,
.umi-print-report .umi-paitent-complain-wrapper {
  display: grid;
  align-items: center;
  grid-template-columns: auto 2fr;
}
.umi-print-report .umi-patinet-name-wrapper {
  margin-right: 1.25rem;
}
.umi-print-report .umi-paitent-complain-wrapper {
  margin-bottom: 0.625rem;
}
