.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-countdown-wrapper {
  margin-bottom: 0.625rem;
  padding: 0.625rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.umi-countdown-wrapper .umi-img-wrapper {
  width: 4.0625rem;
  margin-right: 1.875rem;
}
.umi-countdown-wrapper .umi-time-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.umi-countdown-wrapper .umi-time-section .umi-title {
  margin-bottom: 0.1875rem;
  font-size: 0.875rem;
  font-family: umiFontMed;
}
.umi-countdown-wrapper .umi-time-section .umi-countdown {
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-countdown-wrapper .umi-time-section .umi-time {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.umi-countdown-wrapper .umi-time-section .umi-time-pad {
  position: relative;
  margin-right: 0.3125rem;
  margin-bottom: 0.1875rem;
  padding: 0.3125rem 0.5rem;
  font-size: 1.3125rem;
  border-radius: 0.625rem;
  background-color: #eee7f1;
  color: #551872;
}
.umi-countdown-wrapper .umi-time-section .umi-time-pad:after,
.umi-countdown-wrapper .umi-time-section .umi-time-pad:before {
  content: '';
  position: absolute;
  top: 41%;
  width: 0.125rem;
  height: 20%;
  display: block;
  background-color: #551872;
}
.umi-countdown-wrapper .umi-time-section .umi-time-pad:before {
  left: 0;
}
.umi-countdown-wrapper .umi-time-section .umi-time-pad:after {
  right: 0;
}
.umi-countdown-wrapper .umi-time-section .umi-label {
  font-size: 0.6875rem;
  font-family: umiFontReg;
}
