.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-next-patient {
  margin-bottom: 1.25rem;
  padding: 0.9375rem;
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.umi-next-patient .umi-patient-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.umi-next-patient .umi-patient-data {
  display: flex;
  align-items: center;
}
.umi-next-patient .ant-avatar-image {
  position: relative;
  width: 3.9375rem;
  height: 3.9375rem;
  margin-right: 1.25rem;
  overflow: visible;
  background-color: #00000029;
}
.umi-next-patient .ant-avatar-image:after {
  content: '';
  position: absolute;
  top: 0.1875rem;
  right: 0.1875rem;
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  background-color: #c90000;
}
.umi-next-patient .umi-patient-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.umi-next-patient .umi-patient-name {
  margin-bottom: 0.3125rem;
  font-size: 1.125rem;
  font-weight: bold;
  color: #3d3d3d;
}
.umi-next-patient .umi-patient-id-status {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.umi-next-patient .umi-patient-id {
  margin-right: 0.3125rem;
  font-size: 0.8125rem;
  color: #3d3d3d;
}
.umi-next-patient .umi-icons {
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-next-patient .umi-icons .anticon-qrcode {
  margin-right: 0.625rem;
  font-size: 1.5625rem;
  color: #3d3d3d;
  cursor: pointer;
}
.umi-next-patient .umi-icons .umi-edit-button-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-next-patient .umi-icons .umi-edit-button-wrapper img {
  width: 0.9375rem;
}
.umi-paused-patients {
  padding: 0.9375rem;
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.umi-paused-patients .umi-clinic-title-wrapper {
  margin-bottom: 0.3125rem;
}
.umi-paused-patients .umi-clinic-title-wrapper .umi-clinic-title {
  margin-bottom: 0;
}
.umi-paused-patients .umi-critical-fast {
  margin-bottom: 2rem;
  font-size: 0.6875rem;
  color: #a1a0a0;
}
.umi-paused-patients .umi-paused-patinets-avatars {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.umi-paused-patients .umi-paused-patinets-avatars .umi-avatars {
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-paused-patients .umi-paused-patinets-avatars .ant-avatar-image {
  width: 32px;
  background-color: #00000029;
}
.umi-paused-patients .umi-paused-patinets-avatars .ant-avatar-image:last-of-type {
  z-index: 1;
  margin-left: -1rem;
}
.umi-paused-patients .umi-paused-patinets-avatars .umi-avatar-wrapper {
  z-index: 2;
  width: 32px;
  height: 32px;
  margin-left: -1rem;
  border-radius: 50%;
  background: transparent linear-gradient(180deg, #4f2b79 0%, #9c0067 100%) 0% 0% no-repeat padding-box;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-paused-patients .umi-paused-patinets-avatars .umi-view-all-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-paused-patients .umi-paused-patinets-avatars .anticon-arrow-up {
  font-size: 0.9375rem;
}
.umi-start-patient .umi-ready {
  margin-bottom: 0.625rem;
  font-size: 0.6875rem;
  color: #a1a0a0;
}
.umi-start-patient .umi-start-patient-header {
  margin-left: 1.25rem;
}
.umi-start-patient figure {
  padding: 1.25rem 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.umi-start-patient figure .umi-img-wrapper {
  margin-bottom: 1.25rem;
}
.umi-start-patient figure .umi-press {
  margin-bottom: 1.25rem;
  font-size: 1rem;
  text-align: center;
}
