.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.container {
  width: 85%;
  margin: auto;
}
.container-fluid {
  width: 95%;
  margin: auto;
}
.btn-block {
  width: 100%;
}
.btn-light {
  color: #41085c;
  background-color: #fff;
}
.btn-outline {
  color: #41085c;
  background-color: #fff;
  border: 1px solid #41085c;
}
.btn-outline:hover {
  background-color: #41085c;
  color: #fff;
  border: 1px solid #41085c;
}
.btn-purble {
  background-color: #41085c;
  color: #fff;
}
.btn-purble:hover {
  color: #41085c;
  border-color: #41085c;
}
.squared-btn {
  text-transform: capitalize;
  color: #fff;
  background-color: #520974;
  border-radius: 0.375rem;
  width: 7.5rem;
  height: 2.5rem;
  border: unset;
}
.squared-btn:hover {
  box-shadow: 0 0.9375rem 1.25rem #ecdaf6b5;
  border: unset;
  color: #fff;
  background-color: #520974;
}
.img-fluid {
  max-width: 100%;
  max-height: 100%;
}
.ant-input {
  height: 1.875rem;
  background-color: #fafafa;
  margin-bottom: unset !important;
}
.ant-input-prefix {
  margin-right: 0.625rem;
}
.ant-input-prefix .anticon {
  font-size: 0.9375rem;
  color: #520974;
}
.ant-input-affix-wrapper {
  display: flex;
  align-items: center;
  border-radius: 0.625rem;
  background-color: #fafafa;
  margin-bottom: unset !important;
  height: 2.5rem;
}
.ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper:hover {
  border-color: #520974;
  box-shadow: unset;
}
.ant-input-affix-wrapper > input.ant-input {
  height: 2.375rem;
}
.ant-input-password-icon {
  font-size: 0.9375rem;
  color: #520974;
}
.forget-password a {
  color: #520974;
  cursor: pointer;
  font-size: 0.8125rem;
}
.ant-radio-group {
  display: flex;
}
.ant-radio-button-wrapper {
  height: 2.5rem;
  background-color: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ant-radio-button-wrapper img {
  max-width: 100%;
  max-height: 100%;
  -webkit-filter: opacity(0.2);
          filter: opacity(0.2);
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) {
  border-color: #e8e6f1;
  background-color: #fafafa;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):first-child {
  border-color: unset;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):hover {
  border-color: #e8e6f1;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):active {
  border-color: unset;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):focus-within {
  box-shadow: unset;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled)::before {
  background-color: #e8e6f1;
}
.ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled) img {
  -webkit-filter: opacity(1);
          filter: opacity(1);
}
.ant-radio-button-wrapper:first-child {
  border-top-left-radius: 0.625rem;
  border-bottom-left-radius: 0.625rem;
}
.ant-radio-button-wrapper:last-child {
  border-top-right-radius: 0.625rem;
  border-bottom-right-radius: 0.625rem;
}
.ant-radio-button-wrapper-checked:not([class*=" ant-radio-button-wrapper-disabled"]).ant-radio-button-wrapper:first-child {
  border-right-color: unset !important;
  border-left: unset;
}
.ant-picker,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
  height: 2.5rem;
  border-radius: 0.625rem;
}
.ant-picker:hover,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector:hover {
  border: 0.0625rem solid #520974;
}
.ant-picker .ant-picker-suffix,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector .ant-picker-suffix {
  color: #520974;
}
textarea.ant-input {
  min-height: 4.375rem;
  margin-bottom: 0.9375rem !important;
}
.ant-select-dropdown {
  border-radius: 0.625rem;
}
.ant-select-multiple .ant-select-selector {
  border-radius: 0.625rem;
  border: 0.0625rem solid #e8e6f1;
  background-color: #fafafa;
  height: auto;
  min-height: 2.5rem;
}
.ant-select-multiple .ant-select-selector:hover {
  border: 0.0625rem solid #520974;
  box-shadow: unset;
}
.ant-select-multiple .ant-select-selection-item {
  background: #ece1ed;
}
.ant-select-item {
  border-bottom: 0.0625rem solid #e8e6f1;
}
.ant-select-item:last-child {
  border-bottom: unset;
}
.ant-select-item-option-selected:not(.ant-select-item-option-disabled) {
  background-color: #ece1ed;
}
.ant-select-item-option-selected:not(.ant-select-item-option-disabled) .ant-select-item-option-state {
  color: #ff598b;
}
.ant-form-item-has-error .ant-form-item-explain,
.ant-form-item-has-error .ant-form-item-split {
  font-size: 0.75rem;
}
.ant-input:-ms-input-placeholder {
  font-size: 0.84375rem;
  font-weight: 500;
  color: #520974;
  font-family: "Qanelas-regular";
  text-transform: capitalize;
}
.ant-input:placeholder-shown {
  font-size: 0.84375rem;
  font-weight: 500;
  color: #520974;
  font-family: "Qanelas-regular";
  text-transform: capitalize;
}
.ant-input-number {
  width: 100%;
  height: 2.5rem;
  border-radius: 0.625rem;
  background-color: #fafafa;
}
.ant-input-number:hover,
.ant-input-number:focus {
  border: 0.0625rem solid #520974;
  box-shadow: unset;
}
.carousel-item {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #e2e2e2;
  border-radius: 10px;
  transition: all 0.5s;
}
.hover-shadow {
  box-shadow: 0px 3px 6px #4f2b795e;
}
.arrows {
  bottom: 100%;
  top: unset;
  background-image: url("/imgs/arrow.png");
  width: 50px;
  height: 50px;
}
.arrows-prev {
  right: 4%;
  bottom: 106.8%;
  left: unset;
  transform: rotate(180deg);
}
.slick-prev:before,
.slick-next:before {
  color: transparent;
}
.react-tel-input {
  width: 100%;
  height: 2.5rem;
  border: 0.0625rem solid #e8e6f1;
  background-color: #fafafa;
  border-radius: 0.625rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.react-tel-input .flag-dropdown {
  border: unset !important;
  border-radius: 0.625rem !important;
  position: unset !important;
  width: 20%;
  height: 2.5rem;
  order: 1;
}
.react-tel-input .form-control {
  padding-left: unset !important;
  border: unset !important;
  background: unset !important;
  order: 2;
  width: 80% !Important;
}
.react-tel-input .flag-dropdown:hover .selected-flag {
  background-color: #fafafa !important;
}
.react-tel-input .flag-dropdown:focus .selected-flag {
  background-color: #fafafa !important;
}
.phone-group {
  background-color: #fafafa;
  border-radius: 0.625rem;
  height: 2.625rem;
  border: 0.0625rem solid #e8e6f1;
}
.phone-group:hover {
  border: 0.0625rem solid #520974;
}
.phone-group-flag .react-tel-input {
  border: unset;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: unset;
  border-right: 0.0625rem solid #e8e6f1;
  border-top-left-radius: 0.625rem;
  border-bottom-left-radius: 0.625rem;
}
.phone-group-flag .react-tel-input .form-control {
  width: unset;
}
.phone-group-flag .react-tel-input .flag-dropdown {
  width: unset;
}
.phone-group-flag .react-tel-input .flag-dropdown:hover .selected-flag,
.phone-group-flag .react-tel-input .flag-dropdown:focus .selected-flag {
  border-radius: 1.25rem;
}
.phone-group-input .ant-input {
  border: unset;
}
.phone-group-input .ant-input:hover,
.phone-group-input .ant-input:focus {
  border-color: unset;
}
.umi-container {
  width: 85%;
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5,
p,
figure {
  margin: 0;
}
h4 {
  font-size: 1.1875rem;
}
.umi-btn {
  padding: 0.625rem 2.5rem;
  border: 0;
  border-radius: 0.625rem;
  background-color: #551872;
  color: #fff;
  cursor: pointer;
}
.umi-btn:focus {
  outline: 0;
}
.umi-btn.small {
  padding: 0.375rem 0.625rem;
}
.ant-dropdown-menu {
  border-radius: 0.625rem;
}
.ant-popover-buttons .ant-btn-primary,
.ant-modal-footer .ant-btn.ant-btn-primary {
  background-color: #551872;
  border-color: #551872;
}
.ant-popover-buttons .ant-btn-primary:hover,
.ant-modal-footer .ant-btn.ant-btn-primary:hover {
  background-color: #551872;
  border-color: #551872;
  color: #fff;
}
.ant-popover-buttons .ant-btn-sm:hover,
.ant-modal-footer .ant-btn:hover {
  color: #551872;
  border-color: #551872;
}
.ant-input {
  height: 2.5rem;
  border-radius: 0.625rem;
}
.ant-input:hover,
.ant-input:focus {
  border-color: #551872;
  box-shadow: none;
}
.ant-checkbox:hover .ant-checkbox-inner,
.ant-checkbox-wrapper:hover .ant-checkbox-inner,
.ant-checkbox-checked::after,
.ant-checkbox-input:focus + .ant-checkbox-inner {
  border: 0.0625rem solid #551872;
  border-color: #551872;
}
.ant-checkbox-checked .ant-checkbox-inner {
  border-color: #551872;
  background-color: #551872;
}
.ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-radio-input:focus + .ant-radio-inner {
  border-color: #551872;
  box-shadow: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.ant-select:hover .ant-select-selector,
.ant-select:hover .ant-select-arrow,
.ant-select:focus .ant-select-selector,
.ant-select:active .ant-select-selector {
  border-color: #551872;
  color: #551872;
  outline: 0;
}
.ant-select-selector {
  height: 2.375rem !important;
  border-radius: 0.625rem !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.center {
  text-align: center;
}
.ant-tooltip-inner {
  min-height: auto;
  padding: 3px 5px;
  font-size: 0.75rem;
}
.ant-select-selection-item {
  font-family: umiFontMed;
}
.ant-select-dropdown {
  border-radius: 0.625rem;
}
button:focus {
  outline: none;
}
.ant-col {
  width: 100%;
}
.ant-form-item {
  margin-bottom: 0;
}
.ant-modal-footer .ant-btn-primary {
  background-color: #551872;
}
@media (max-width: 1199px) {
  .umi-container {
    width: 95%;
  }
}
