.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-reservation-popup-wrapper {
  position: relative;
}
.umi-reservation-popup-wrapper .umi-resarvation-wrapper {
  position: fixed;
  bottom: 3.125rem;
  right: 1.5625rem;
  z-index: 1;
}
.umi-reservation-popup-wrapper .umi-resarvation-wrapper .umi-patients-reservation {
  height: 18.75rem;
  overflow-y: auto;
}
.umi-reservation-popup-wrapper .umi-display-reservation .ant-badge-count,
.umi-reservation-popup-wrapper .umi-display-reservation .anticon-user {
  display: none;
}
.umi-reservation-popup-wrapper .umi-reservation-popup {
  position: fixed;
  bottom: 1.25rem;
  right: 1.25rem;
  z-index: 2;
  cursor: pointer;
}
.umi-reservation-popup-wrapper .umi-reservation-popup .ant-avatar-icon {
  width: 3.75rem;
  height: 3.75rem;
  border: 0.3125rem solid #eee7f1;
  border-radius: 50%;
  background: transparent linear-gradient(180deg, #4f2b79 0%, #9c0067 100%) 0% 0% no-repeat padding-box;
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-reservation-popup-wrapper .umi-reservation-popup .ant-badge-multiple-words {
  top: 0.5rem;
  right: 0.5rem;
  width: 1rem;
  height: 1rem;
  padding: 0;
  border-radius: 50%;
  font-size: 0.6875rem;
  line-height: 1.0625rem;
  background-color: #ffd500;
  color: #3d3d3d;
}
.umi-patient-session-info {
  padding: 1.875rem 0;
}
.umi-patient-session-info .umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-session-info {
  align-items: flex-start;
}
.umi-patient-session-info .umi-patient-reservation-wrapper .umi-patient-wrapper .ant-avatar-image {
  width: 5rem;
  height: 5rem;
}
.umi-patient-session-info .umi-patient-reservation-wrapper .umi-patient-wrapper .ant-avatar-image:after {
  display: none;
}
.umi-patient-session-info .umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-name {
  font-size: 1.4375rem;
}
.umi-patient-session-info .umi-patient-reservation-wrapper .umi-patient-wrapper .ant-tag-red {
  width: 100px;
  height: 30px;
  font-weight: 400;
  line-height: 28px;
}
.umi-patient-session-info .umi-countdown-wrapper {
  border: 0;
  justify-content: flex-end;
  box-shadow: none;
}
.ant-tabs-bar {
  margin-bottom: 1.875rem !important;
  box-shadow: 0 0 0.625rem #ddd;
}
.ant-tabs-bar .ant-tabs-nav .ant-tabs-tab-active {
  font-weight: 600 !important;
  color: #551872 !important;
}
.ant-tabs-bar .ant-tabs-nav .ant-tabs-tab:hover {
  font-weight: 600 !important;
  color: #551872 !important;
}
.ant-tabs-bar .ant-tabs-nav .ant-tabs-ink-bar {
  background-color: #551872;
}
.ant-tabs-top-content .umi-datepicker-new {
  margin-bottom: 1.5625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ant-tabs-top-content .umi-datepicker-new .umi-datepicker {
  display: flex;
  align-items: center;
}
.ant-tabs-top-content .umi-datepicker-new .ant-picker-range {
  margin: 0 1.875rem;
}
.ant-tabs-top-content .umi-btn-wrapper {
  text-align: center;
}
.ant-tabs-top-content .umi-btn-wrapper .umi-btn {
  padding: 0.375rem 1.25rem;
  border: 1px solid #f1ecec;
  background-color: transparent;
  color: #3d3d3d;
}
.ant-tabs-top-content .umi-btn-wrapper .umi-btn:hover {
  background-color: #551872;
  color: #fff;
}
.ant-tabs-top-content .umi-session-complain {
  margin-bottom: 1.5625rem;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.umi-patient-examination-info {
  display: grid;
  grid-template-columns: 15% 85%;
}
.umi-examination-status,
.umi-recomandation-title {
  margin-bottom: 1rem;
}
.umi-examination-type,
.umi-examination-status,
.umi-recomandation-title {
  font-weight: 600;
}
.more-icon-wrapper {
  cursor: pointer;
}
.more-icon-wrapper .anticon-more {
  font-size: 1.4375rem;
}
.umi-textarea-icons {
  position: relative;
}
.umi-textarea-icons .umi-icons-wrapper {
  position: absolute;
  top: 0.625rem;
  right: 0.625rem;
  z-index: 1;
}
.umi-textarea-icons .umi-icons-wrapper img {
  width: 0.625rem;
}
.inputs-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0 10px;
}
.ant-collapse-item .ant-collapse-content {
  overflow: visible;
}
.umi-session-history-wrapper .ant-form-item {
  margin-bottom: 0.625rem;
}
