.box-style {
  border: 0.03125rem solid #f1ecec;
  border-radius: 0.625rem;
  box-shadow: 0 0 0.625rem #0000001a;
  background-color: #fff;
}
.clinic-title {
  font-size: 0.9375rem;
  font-family: umiFontMed;
  color: #3d3d3d;
}
.clinic-title:after {
  content: '';
  width: 1.25rem;
  display: block;
  border: 1px solid #ecc1da;
}
.gridCenter {
  display: grid;
  justify-content: center;
  align-content: center;
}
.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBetween {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.flexVerticalCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon-circle-wrapper {
  padding: 0.5rem;
  border: 0.0625rem solid #f1ecec;
  border-radius: 50%;
  cursor: pointer;
  display: grid;
  justify-content: center;
  align-content: center;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-data {
  display: flex;
  align-items: flex-start;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .ant-avatar-image {
  position: relative;
  width: 3.3125rem;
  height: 3.3125rem;
  margin-right: 1.25rem;
  overflow: visible;
  background-color: #00000029;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .ant-avatar-image:after {
  content: '';
  position: absolute;
  top: 0.1875rem;
  right: 0;
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  background-color: #07cd15;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-info {
  margin-right: 0.625rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-items: flex-start;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-name {
  margin-right: 0.625rem;
  font-size: 0.8125rem;
  color: #3d3d3d;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-id-status {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-img-wrapper {
  margin-right: 0 !important;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-id {
  margin-right: 0.3125rem;
  font-size: 0.8125rem;
  color: #3d3d3d;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .ant-tag-red {
  width: 3.75rem;
  height: 1.5625rem;
  margin: 0;
  border-radius: 0.6875rem;
  border-color: transparent;
  font-size: 0.6875rem;
  line-height: 1.5625rem;
  text-align: center;
  font-family: umiFontMed;
  font-weight: 600;
  letter-spacing: 0.6px;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-patient-flag {
  margin-left: 0.625rem;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .anticon-flag {
  margin-right: 0.3125rem;
  color: #c90000;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-controls {
  margin-top: 1.5625rem;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-btn.small {
  margin-right: 0.5rem;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-btn.small.style {
  border: 1px solid #f1ecec;
  background-color: #fff;
  color: #3d3d3d;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-btn.small a {
  color: #fff;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-controls .ant-dropdown {
  margin-top: 0.5rem;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .umi-edit-button-wrapper {
  display: flex;
}
.umi-patient-reservation-wrapper .umi-patient-wrapper .anticon {
  font-size: 1.125rem;
  cursor: pointer;
}
