body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@font-face {
  font-family: umiFontMed;
  src: url(/react/static/media/Qanelas-Medium.fd0bbe77.otf);
  src: url(/react/static/media/Qanelas-Medium.d9531ea5.ttf);
  font-weight: 600;
}
@font-face {
  font-family: umiFontReg;
  src: url(/react/static/media/Qanelas-Regular.ed4b2c69.otf);
  src: url(/react/static/media/Qanelas-Regular.87200a9c.ttf);
  font-weight: 400;
}
* {
  font-family: umiFontReg;
}
@font-face {
  font-family: "Qanelas-Bold";
  src: url(/react/static/media/Qanelas-Bold.3322174d.ttf);
  src: url(/react/static/media/Qanelas-Bold.27cff607.eot);
  src: url(/react/static/media/Qanelas-Bold.806c3ab2.otf);
  src: url(/react/static/media/Qanelas-Bold.d682cf5f.woff);
  src: url(/react/static/media/Qanelas-Bold.427425eb.woff2);
}
@font-face {
  font-family: "Qanelas-meduim";
  src: url(/react/static/media/Qanelas-Medium.d9531ea5.ttf);
  src: url(/react/static/media/Qanelas-Medium.875b8413.eot);
  src: url(/react/static/media/Qanelas-Medium.22e551d1.woff);
  src: url(/react/static/media/Qanelas-Medium.55711689.woff2);
  src: url(/react/static/media/Qanelas-Medium.fd0bbe77.otf);
}
@font-face {
  font-family: "Qanelas-regular";
  src: url(/react/static/media/Qanelas-Regular.87200a9c.ttf);
  src: url(/react/static/media/Qanelas-Regular.1485a474.eot);
  src: url(/react/static/media/Qanelas-Regular.ed4b2c69.otf);
  src: url(/react/static/media/Qanelas-Regular.668ae59e.woff);
  src: url(/react/static/media/Qanelas-Regular.10fa10f2.woff2);
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Qanelas-Bold";
  text-transform: capitalize;
}
p,
button {
  font-family: "Qanelas-regular";
}
span,
a {
  font-family: "Qanelas-meduim";
}
